*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 50px;
}
.container{
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-areas: "a1 a1" "a2 a3" "a4 a4";
  grid-template-rows: 13vh 74vh 13vh;
  grid-template-columns: 20vw;
}
#header{
  background-color: rgb(36, 36, 184);
  grid-area: a1;
  text-align: center;
}
#header h1{
  color: white;
  filter: drop-shadow(5px 5px 5px black);
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
}
#lista{
  box-shadow: 1px 0px 10px black;
  grid-area: a2;
  overflow-y: auto;
}
#containerList{
  margin: 1rem 0 0 1rem;
}
#lista ul >li{
  font-size: .8rem;
  color: rgb(41, 40, 40);
  font-weight: bold;
  list-style: none;
  cursor: pointer;
}
.done{
  text-decoration: line-through;
}
#formulario{
  grid-area: a3;
}
#form{
  width: 18rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: auto;
}
#form >input{
  text-align: center;
  width: 9rem;
  border-radius: .2rem;
  border-top: none;
  filter: drop-shadow(5px 5px 5px black);
  font-size: .8rem;
  padding: .2rem;
  color: rgb(59, 58, 58);
}
#btn{
  display: flex;
  flex-wrap: wrap;
}
#btn > input{
  margin: 0 .5rem;
  cursor: pointer;
  font-size: .8rem;
  padding: .4rem;
  border: none;
  border-radius: .2rem;
  color: white;
  background-color: rgba(72, 68, 68, 0.672);
}
#footer{
  background-color: rgb(36, 36, 184);
  grid-area: a4;
  text-align: center;
}
#footer p{
  color: white;
  font-size: 1.1rem;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
@media screen and (max-width:1200px){
  *{
    font-size: 35px;
  }
}
@media screen and (max-width:800px){
  *{
    font-size: 20px;
  }
  .container{
    grid-template-columns: 40vw;
  }
  #form{
    width: 10rem;
    margin: auto;
  }
  #btn{
    flex-direction: column;
    gap: 20px;
  }
  #lista ul >li{
    font-size: 1rem;
    line-height: 28px;
  }
}
